import React, { useState } from 'react';
import styled from 'styled-components';
import { GameBoard } from './components/GameBoard';
import { MainMenu } from './components/MainMenu';
import { Scoreboard } from './components/Scoreboard';
import { useGameLogic } from './hooks/useGameLogic';
import { Planet as PlanetType, AttackPercentage } from './types/game';

const AppContainer = styled.div`
  min-height: 100vh;
  background: linear-gradient(135deg, #0f0f23 0%, #1a1a2e 50%, #16213e 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Arial', sans-serif;
`;

const GameTitle = styled.h1`
  color: #FFD700;
  font-size: 48px;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  text-align: center;
`;

const GameSubtitle = styled.p`
  color: #ccc;
  font-size: 18px;
  margin-bottom: 40px;
  text-align: center;
  max-width: 600px;
  line-height: 1.5;
`;

const Instructions = styled.div`
  color: #aaa;
  font-size: 14px;
  text-align: center;
  margin-top: 20px;
  max-width: 600px;
  line-height: 1.6;
`;

type AppState = 'menu' | 'game' | 'scoreboard';

function App() {
  const [appState, setAppState] = useState<AppState>('menu');
  const [username, setUsername] = useState<string>('');
  const { gameState, selectPlanet, executeAttack, clearSelection, nextLevel } = useGameLogic(username);

  const handleStartGame = (userInput: string) => {
    setUsername(userInput);
    setAppState('game');
  };

  const handleShowScoreboard = () => {
    setAppState('scoreboard');
  };

  const handleBackToMenu = () => {
    setAppState('menu');
  };

  const handlePlanetClick = (planet: PlanetType) => {
    selectPlanet(planet);
  };

  const handleAttack = (percentage: AttackPercentage) => {
    executeAttack(percentage);
  };

  const renderContent = () => {
    switch (appState) {
      case 'menu':
        return (
          <MainMenu
            onStartGame={handleStartGame}
            onShowScoreboard={handleShowScoreboard}
          />
        );
      case 'game':
        return (
          <>
            <GameBoard
              gameState={gameState}
              onPlanetClick={handlePlanetClick}
              onAttack={handleAttack}
              onNextLevel={nextLevel}
              onClearSelection={clearSelection}
            />
            <Instructions>
              <strong> NASIL OYNANIR:</strong><br />
              <br />
              <strong> Temel Mekanikler:</strong><br />
              1. <strong>Gezegen Seimi:</strong> Kendi gezegenlerinizi (yeil) tklayarak sein<br />
              2. <strong>oklu Seim:</strong> Birden fazla gezegen seebilirsiniz (tekrar tklayarak karabilirsiniz)<br />
              3. <strong>Hedef Seimi:</strong> Dman veya tarafsz gezegene tklayarak hedef belirleyin<br />
              4. <strong>Saldr:</strong> %25, %50 veya %100 saldr gc sein<br />
              <br />
              <strong> Sava Sistemi:</strong><br />
               Seili gezegenlerin toplam askerleri hedefe saldrr<br />
               Askerler her saniye otomatik retilir<br />
               Tm dman askerleri yok edildiinde gezegen ele geirilir<br />
              <br />
              <strong> AI Oyuncular:</strong><br />
               Her blmde farkl sayda AI oyuncu bulunur<br />
               AI'lar srayla hamle yapar<br />
               Zorluk seviyesi blmler ilerledike artar<br />
              <br />
              <strong> Blm Sistemi:</strong><br />
               5 blm bulunur<br />
               Her blmde daha fazla gezegen ve AI<br />
               Tm dmanlar yenerek blm tamamlayn<br />
              <br />
              <strong> Kazanma Koullar:</strong><br />
               Tm dman gezegenlerini ele geirin<br />
               VEYA dmann tm askerlerini yok edin<br />
               5 blm tamamlayarak oyunu bitirin!<br />
              <br />
              <strong> pular:</strong><br />
               nce tarafsz gezegenleri ele geirin<br />
               Byk gezegenler daha hzl asker retir<br />
               Saldr gcn dikkatli sein<br />
               st ubukta seim bilgilerini takip edin
            </Instructions>
          </>
        );
      case 'scoreboard':
        return (
          <Scoreboard onBackToMenu={handleBackToMenu} />
        );
      default:
        return null;
    }
  };

  return (
    <AppContainer>
      <GameTitle>SpaceWars</GameTitle>
      <GameSubtitle>
        Gezegen Fetih Strateji Oyunu
      </GameSubtitle>
      {renderContent()}
    </AppContainer>
  );
}

export default App;
